@import '../../styles/style.scss';

.navigation {
  position: fixed;
  top: 0;
  left: 0;
  width: 4vmax;
  height: 90vmin;
  display:flex;
  flex-direction: column;
  padding-left: 2vmin;
  padding-top: 1vmin;
  justify-content: space-around;
  align-items: center;
}

.square {
  width: 2.5vmin;
  padding: 1vmax;
  border: 0.1vmin solid white;
  height: 15vmax;
  margin-top: 2vmin;
  display: flex;
  align-content: space-around;
  justify-content: center;
}

.square-2 {
  width: 5vmin;
  height: 6vmax;
  margin-top: 2vmin;
  display: flex;
  align-content: space-around;
  justify-content: center;
}

.square p {
  writing-mode: vertical-rl;
  transform: rotate(180deg);
  margin: 0;
  padding: 0;
  font-size: 2.5ex;
}

.icon {
  margin: 0;
  padding: 0;
}
